<template>
  <div class="login">
    <div class="fh">
    <img @click="goback" src="../../assets/fh.png" alt="">
    </div>
    <span>手机号注册/登录</span>
    <p>首次登录自动为您注册</p>
    <div class="login_1">
      <input type="text" v-model="msg.phone" placeholder="手机号">
      <div class="yzm">
      <input type="password" v-model="msg.yzm" placeholder="请输入4位验证码">
      <span>获取验证码</span>
      </div>
    </div>
    <mt-button size="large" type="danger" @click="logining">登录</mt-button>
    <div class="login_2">
      <span @click="goback"> 密码登录</span>
      <span>|</span>
      <span>忘记密码？</span>
      <span>|</span>
      <span>遇到问题</span>
    </div>
    <div class="login_3">
      <img src="../../assets/QQ.png" alt="">
      <img src="../../assets/wei-xin.png" alt="">
      <img src="../../assets/weibo.png" alt="">
      <img src="../../assets/youxiang.png" alt="">
    </div>
    <div class="login_4">
      <span>注册/登录即代表同意</span>
      <span>《用户协议》</span>
      <span>和</span>
      <span>《隐私协议》</span>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      msg:{
        phone:"",
        yzm:"",
      }
      
    }
  },
  methods:{
    logining(){
      let _this=this;
      if(this.msg.uname===''||this.msg.upwd===''){
        this.$toast("手机号或验证码不能为空")
      }else{
        this.axios({
          method:"get",
          url:"/Register",
          data:this.msg
        }).then(res=>{
          console.log(res);
          _this.$router.push('/')
          this.$toast('登录成功')
        }).catch(err=>{
          this.$toast('手机或验证码错误');

        })
      }
    },
    goback(){
      this.$router.back();
    },
    
  }
}
</script>
<style scoped>
.fh{
  text-align: start;
}
.fh>img{
  width:25px;
}
.login{
  background-color: #fff;
  padding: 20px;
  text-align: center;
}
.login>span{
  font-weight: bold;
  font-size: 30px;
}
.login>p{
  font-size: 14px;
  color: #aaa;
}
.login_1{
  margin-top: 50px;
  padding-bottom: 50px;
}
input{
    background-color: #fff;
    border-radius: 0;
    border-bottom: 1px solid #ccc;
    width: 89%;
    height: 40px;
    padding-left: 40px;
    margin-top: 10px;
    
  }

  .login_1>input:first-child{
    background-image: url("../../assets/phone.png");
  }
  .yzm{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #ccc;
  }
  .yzm>input{
    background-image: url("../../assets/yzm.png");
    width: 50%;
    border-bottom: 0;
  }
  .yzm>span{
    height: 14px;
    margin-top:18px;
    border: 1px solid orangered;
    padding:6px 8px;
    font-size: 12px;
    color: orangered;
  }
  .login_2{
    margin-top: 20px;
    color: #aaa;
  }
  .login_2>span{
  padding-right: 5px;
  }
  .login_3{
    margin-top:300px;
    width: 100%;
    display: flex;
    justify-content: space-around;
  }
  .login_3>img{
    width: 60px;
  }
  .login_4{
    margin-top: 20px;
  }
  .login_4>span{
    font-size: 14px;
  }
  .login_4>span:first-child,.login_4>span:nth-child(3){
    color: #aaa;
  }
</style>